import * as XLSX from 'xlsx';
import { getBase64 } from '../file-helper';

const increasingSequence = (arr) => !arr.some((val, idx) => val !== arr[0] + idx);
const arrayValuesIncluded = (arr, target) => target.every(v => arr.includes(v));

const validateBudget = async (format, f, indicatorsResult, budget_sheet = 'template_expenditure',
  relation_table_sheet = 'template_relation_table') => {
  const errors = {
    sheet: [],
    expenditure: [],
    program_id: [],
    out_of_programs: [],
    empty: [],
    labels: [],
  };

  let error = false;

  if (format !== 'xlsx' && format !== 'xls') {
    throw new Error("Wrong format file");
  } else {
    const f64 = await getBase64(f);
    const file = XLSX.read(f64, { type: 'base64' });

    var program_exp = []
    var indicator_labels = []
    var Bs = []
    var B_dict = {}

    // Verify errors and storage programs and Bs
    const expend = file.Sheets[budget_sheet]
    const prog = file.Sheets[relation_table_sheet]

    if (!expend || expend.length === 0 || !prog || prog.length === 0) {
      errors.sheet.push(`The file uploaded do not contain a sheet with the name: ${(!expend || expend.length === 0 ) ? budget_sheet : relation_table_sheet}`);
      error = true;
    } else {
      Object.keys(expend).map((k) => {
        const value = expend[k];
        if (value['t'] !== 'n' && value['v'] !== 'program_ID' && value['v'] !== 'expenditure' && k !== '!ref' && k !== '!margins') {
          errors.expenditure.push(k);
          error = true;
        } else {
          const re_A = new RegExp("^A")
          if (re_A.test(k) && k !== 'A1') { program_exp.push(value['v']) }
          const re_B = new RegExp("^B")
          if (re_B.test(k) && k !== 'B1') { Bs.push(value['v']) }
        }
      });

      if (program_exp.length !== Bs.length) {
        errors.expenditure.push("You must have set expenditure for each program_id");
        error = true;
      }

      if (!increasingSequence(program_exp)) {
        errors.expenditure.push("Expenditure must be an increasing sequence of numbers");
        error = true;
      }

      if ( Bs.some(v => v <= 0) ) {
        errors.expenditure.push("Expenditure must have positive integers");
        error = true;
      }

      // Verify if relations table has correct values
      Object.keys(prog).map((k) => {
        const value = prog[k];
        const re_prog = new RegExp("^A");

        if (value['t'] !== 'n' && value['v'] !== 'indicator_label' && !/^program/.test(value['v']) && !re_prog.test(k)
          && k !== '!ref' && k !== '!margins') {
          errors.program_id.push(k);
          error = true;
        }
        if (!program_exp.includes(value['v']) && value['t'] === 'n' && !re_prog.test(k)) {
          errors.out_of_programs.push(k);
          error = true;
        }

        if (value['t'] === 's' && value['v'] !== 'indicator_label' && !/^program/.test(value['v']) && re_prog.test(k)) {
          indicator_labels.push(value['v']);
        }
      });

      const instrumentalLabels = indicatorsResult.ID.filter((a, i) => indicatorsResult.R[i] === 1);

      if (!arrayValuesIncluded(instrumentalLabels, indicator_labels) || !arrayValuesIncluded(indicator_labels, instrumentalLabels)) {
        errors.labels.push(`You are missing several indicator labels in the budget file ${instrumentalLabels.filter((a) => !indicator_labels.includes(a)).join(', ')}`);
        error = true;
      }

      // Storage into B_dict arrays programs.
      var range = XLSX.utils.decode_range(prog['!ref']);
      for (var r = (range.s.r + 1); r <= range.e.r; r += 1) {
        if (!(prog[XLSX.utils.encode_cell({ c: 0, r: r })] === undefined)) {
          const aColumnValue = prog[XLSX.utils.encode_cell({ c: 0, r: r })]['v'];
          const aColumnIndex = indicatorsResult.ID.indexOf(aColumnValue);

          if (aColumnIndex !== -1) {
            B_dict[aColumnIndex] = [];
            for (var c = (range.s.c + 1); c <= range.e.c; c += 1) {
              if (!(prog[XLSX.utils.encode_cell({ c: c, r: r })] === undefined)) {
                B_dict[aColumnIndex]
                  .push(prog[XLSX.utils.encode_cell({ c: c, r: r })]['v'] - 1)
              }
            }
            if (B_dict[aColumnIndex].length === 0) {
              errors.out_of_programs.push(XLSX.utils.encode_cell({ c: 0, r: r }));
              error = true;
            }
          }
        }
      }
      if (!Object.values(errors).filter(i => i.length > 0).length && Bs.length > 0) {
        return { Bs, B_dict }
      } else {
        if (Bs.length === 0) {
          errors.empty.push('The uploaded file structure does not meet the template format or is empty');
          error = true;
        }
      }
    }
    return {
      errors,
      error
    }
  }
}

export {
  validateBudget
}