import React, { Fragment } from 'react';
import { Box, Button, Container, FormControl, FormControlLabel, Grid, Radio, RadioGroup, TextField, Typography } from '@mui/material';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import budgetTemplate from '../../../assets/templates/template_budget.xlsx';
import styles from './steps.module.scss';
import { texts_budget as texts } from '../../../global/texts/texts-budget-page.js'
import { CustomBreadcrumbs } from '../breadcrumbs';
import { MediaUploader } from '../../../common/media-uploader/media-uploader';
import { FileValidation } from './validations/fileValidation';
import { validateBudget } from '../../../helpers/validations/validateBudget';
import { getFileExtention } from '../../../helpers/file-helper';
import { SiteTitle } from '../../../components/SiteTitle';

const Budget = (props) => {
  const { nextStep, simulationState, budgetFile, setBudgetFile, indicatorsResult, setBudgetResults, budget, setBudget } = props;
  const [budgetType, setBudgetType] = React.useState('single');
  const [verified, setVerified] = React.useState(false);
  const [validationResult, setValidationResult] = React.useState(null);


  const uploadFile = async (file) => {
    if (file && file.name) {
      const extention = getFileExtention(file);
      const fileValidation = await validateBudget(extention, file, indicatorsResult);
      setBudgetFile(file);
      if (
        !fileValidation.error
      ) {
        setVerified(true);
        setValidationResult(fileValidation);
        setBudgetResults(fileValidation);
      } else {
        setVerified(false);
        setValidationResult(fileValidation);
      }
    } else {
      setBudgetFile([]);
      setVerified(false);
      setValidationResult(null);
    }
  }

  const canGoNext = () => {
    if (budgetType === 'single' && budget && budget !== '') {
      setBudgetFile(null);
      setVerified(true);
    } else if (validationResult && !validationResult.error) {
      setBudget('');
      setVerified(true);
    } else {
      setVerified(false);
    }
  }
  React.useEffect(() => {
    canGoNext();
  }, [budgetType, budget, validationResult]);
  return (
    <Container
      maxWidth="xl"
      sx={{
        paddingY: (theme) => [ theme.spacing(4), theme.spacing(4), theme.spacing(8) ]
      }}
    >
      <Grid container rowSpacing={4} columnSpacing={8} columnns={{ xs: 4, sm: 8, md: 12, lg: 12 }}>
        <Grid item xs={12} md={5}>
          <SiteTitle />
        </Grid>
        <Grid item xs={12} md={7}>
          <CustomBreadcrumbs selected={simulationState} />
        </Grid>
        <Grid item xs={12} md={5}>
          <Typography variant="h1">
            Budget
          </Typography>
          <Typography variant='body1' mt={4}>
            { texts.INFORMATION_DESCRIPTION }
          </Typography>
        </Grid>
        <Grid item xs={12} md={7}>
          <FormControl>
            <RadioGroup
              value={budgetType}
              onChange={(e) => setBudgetType(e.target.value)}
            >
              <FormControlLabel
                value={'single'}
                control={<Radio />}
                label={<Typography variant='h5'>Specify the total budget</Typography>}
              />
              <Typography variant='body2' mt={1}>
                { texts.PROVIDE_BUDGET_DESCRIPTION }
              </Typography>
              <FormControl variant="standard">
                <TextField
                  sx={{
                    marginTop: (theme) => theme.spacing(4),
                    marginBottom: (theme) => theme.spacing(4)
                  }}
                  id="budget"
                  variant="outlined"
                  label="Total budget"
                  value={budget}
                  onChange={(e) => setBudget(e.target.value)}
                  disabled={budgetType !== 'single'}
                />

              </FormControl>
              <div className={styles.separator} />
              <FormControlLabel
                value={'disaggregated'}
                control={<Radio />}
                label={<Typography variant='h5'>Upload the disaggregated budget </Typography>}
                />
              <Typography variant='body2' mt={1} mb={2}>
                { texts.UPLOAD_FILE_DESCRIPTION }
              </Typography>
              { budgetType === 'disaggregated' && (
                <Fragment>
                  <Button
                    href={budgetTemplate}
                    variant='outlined'
                    sx={{
                      width: 'min-content',
                      whiteSpace: 'nowrap',
                    }}
                  >
                    Download template
                  </Button>
                  <MediaUploader
                    maxFiles={1}
                    previewsPerLine={1}
                    supportedExtensions={['xlsx', 'xls']}
                    onFileSelection={(files) => {
                      if (files) {
                        uploadFile(files[0]);
                      } else {
                        uploadFile([]);
                      }
                    }}
                    disabled={budgetType === 'single'}
                  />
                  {budgetFile
                    && budgetFile.length !== 0
                    && validationResult
                    && budgetFile.name
                    && (
                      <FileValidation component="budget" verified={verified} validationResult={validationResult} />
                    )}
                </Fragment>
              )}
            </RadioGroup>
          </FormControl>
          <Box mt={4} textAlign="right">
            <Button
              variant='contained'
              size='large'
              onClick={() => {nextStep(); setVerified(true)}}
              disabled={!verified}
            >
              Next
              <NavigateNextIcon />
            </Button>
          </Box>
        </Grid>
      </Grid>
    </Container>
  )
}

export { Budget };

export default Budget;